import '../ControlPanelStyle.css';
import firebase from 'firebase';
import React, { useState } from 'react';

var ChangePass;
var PassChange;
var oldpassword;
var password;
var passwordrep;
var Data;
var user;
export function PwChange(key) {
  if(document.getElementById("user-role").innerHTML === "Administratorius"){user = "Admin"}
  else if((document.getElementById("user-role").innerHTML === "Buhalteris(-ė)")||(document.getElementById("user-role").innerHTML === "Vadovas(-ė)")){user = "Staff"}
  else{user = "User"}
  return(
    console.log(key),
    oldpassword = document.getElementById('old-password').value,
    password = document.getElementById('password').value,
    passwordrep = document.getElementById('password-rep').value,
    console.log(oldpassword + " " + password + " " + passwordrep),
    ChangePass = firebase.database().ref(user).orderByKey().equalTo(key),
    ChangePass.once('value', (snapshot) => {
      snapshot.forEach((childSnapshot) => {
          Data = childSnapshot.val();
      });
      if(oldpassword === Data.password){
        if(password === passwordrep){
          if((password.length >= 8)&&(password.length <= 20)){
            PassChange = firebase.database().ref(user + "/" + key);
            PassChange.update({
              password: password
            });
            document.getElementById('old-password').value = "";
            document.getElementById('password').value = "";
            document.getElementById('password-rep').value = "";
            document.getElementById("change-pw").style.color = "green";
            document.getElementById("change-pw").innerHTML = "Slaptažodis sėkmingai pakeistas";
            document.getElementById("old-password").innerHTML = "";
            document.getElementById("password").innerHTML = "";
            document.getElementById("password-rep").innerHTML = "";

          }
          else{
            document.getElementById("change-pw").innerHTML = "Slaptažodis turi buti nuo 8 iki 20 simbolių";
            document.getElementById("change-pw").style.color = "red";
          }

        }
        else{
          document.getElementById("change-pw").innerHTML = "Slaptažodžiai nesutampa";
          document.getElementById("change-pw").style.color = "red";
        }

      }
      else{
        document.getElementById("change-pw").innerHTML = "Neteisingas slaptažodis";
        document.getElementById("change-pw").style.color = "red";
      }
    })  
  );  
};

function ChangePassword(props) {
  var t;
  var t1, t2, t3;
  var pass = /^[0-9a-zA-Z]+$/;
  const [PO, setPO] = useState('');
  t1 = "";
  for(var i = 0; i < PO.length; i++){
    t = PO.charAt(i);
    if(t.match(pass)){
      t1 = t1 + t;
    }
  }
  const [PN, setPN] = useState('');
  t2 = "";
  for(var j = 0; j < PN.length; j++){
    t = PN.charAt(j);
    if(t.match(pass)){
      t2 = t2 + t;
    }
  }
  const [PR, setPR] = useState('');
  t3 = "";
  for(var n = 0; n < PR.length; n++){
    t = PR.charAt(n);
    if(t.match(pass)){
      t3 = t3 + t;
    }
  }
  return (
    <div className="dashboard-page">
      <h2>Slaptažodžio keitimas</h2>
      <div>
        <div className="name">Dabartinis slaptažodis</div>
        <input id="old-password" placeholder="Dabartinis slaptažodis" className="input-box" type="password" value={t1} onChange={event => setPO(event.target.value)} maxLength="20" />
      </div>
      <div>
        <div className="password">Naujas slaptažodis</div>
        <input id="password" placeholder="Naujas slaptažodis" className="input-box " type="password" value={t2} onChange={event => setPN(event.target.value)} maxLength="20" />
      </div>
      <div>
        <div className="password">Pakartoti slaptažodį</div>
        <input id="password-rep" placeholder="Naujas slaptažodis" className="input-box pw" type="password" value={t3} onChange={event => setPR(event.target.value)} maxLength="20" />
      </div>
      <div id="change-pw"></div>
      <div className="buttons">
        <button id="autobutton" className="pw-button" onClick={() => PwChange(props.id)}>Keisti slaptažodį</button>
      </div>
    </div>
  );
}

export default ChangePassword;