import '../style.css';
import firebase from 'firebase';
import ReactDOM from 'react-dom';
import Start from './Start';
import RegisterFinish from './Register-finish';
import React, { useState } from 'react';

var EmployeesRegCheck;
var kodas;
var idkodas;
var Data;
var key;
export function RegCheck() {
  return(
      kodas = document.getElementById('code').value,
      idkodas = document.getElementById('tempcode').value,
      EmployeesRegCheck = firebase.database().ref("User").orderByChild("code").equalTo(kodas),
      EmployeesRegCheck.once('value', (snapshot) => {
        snapshot.forEach((childSnapshot) => {
            Data = childSnapshot.val();
            key = childSnapshot.key;
        });
        if(kodas.length < 11){
          if((Data !== undefined)&&(idkodas === Data.userid)){
            if((kodas === Data.code)&&(idkodas === Data.userid)){
              ReactDOM.render(<RegisterFinish id={key}/>, document.getElementById('root'))
            }
          }
          else document.getElementById("reg-error").innerHTML = "Indentifikacinis kodas neteisingas";
        }
        else document.getElementById("reg-error").innerHTML = "Asmens kodas turi 11 skaitmenų";
      })
  );  
};

function Register() {
  var t;
  var nt;
  var rt;
  var akodas = /^[0-9]+$/;
  var idkodas = /^[0-9a-fA-F]+$/;
  const [AK, setAK] = useState('');
  nt = "";
  for(var i = 0; i < AK.length; i++){
    t = AK.charAt(i);
    if(t.match(akodas)){
      nt = nt + t;
    }
  }
  const [IK, setIK] = useState('');
  rt = "";
  for(var j = 0; j < IK.length; j++){
    t = IK.charAt(j);
    if(t.match(idkodas)){
      rt = rt + t;
    }
  }
  return (
    <div className="container page-login">
      <div className="small-container">
        <div id="user" className="content">
          <h2>Registracija</h2>
          <div>
            <div className="name">Asmens kodas</div>
            <input id="code" placeholder="Asmens kodas" className="input-box" type="text" value={nt} onChange={event => setAK(event.target.value)} maxLength="11"/>
          </div>
          <div>
            <div className="password">Indentifikacinis kodas</div>
            <input id="tempcode" placeholder="Indentifikacininis kodas" className="input-box pw" type="text" value={rt} onChange={event => setIK(event.target.value)} maxLength="6" />
          </div>
          <div id="reg-error"/>
          <div className="buttons">
            <button id="autobutton" className="menubutton" onClick={() => RegCheck()}>Registruotis</button>
            <button className="menubutton" onClick={() => ReactDOM.render(<Start />, document.getElementById('root'))}>Grįžti atgal</button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Register;