import '../style.css';
import ReactDOM from 'react-dom';
import Start from './Start';
import {getLogin} from './components/get-login';
import React, { useState } from 'react';

function Login() {
  var t;
  var nt;
  var rt;
  var usern = /^[0-9a-zA-Z-_.]+$/;
  var passw = /^[0-9a-zA-Z]+$/;
  const [UN, setUN] = useState('');
  nt = "";
  for(var i = 0; i < UN.length; i++){
    t = UN.charAt(i);
    if(t.match(usern)){
      nt = nt + t;
    }
  }
  const [PW, setPW] = useState('');
  rt = "";
  for(var j = 0; j < PW.length; j++){
    t = PW.charAt(j);
    if(t.match(passw)){
      rt = rt + t;
    }
  }
  return (
    <div className="container page-login">
      <div className="small-container">
        <div className="content">
          <h2>Prisijungimas</h2>
          <div>
            <label className="name">Prisijungimo vardas</label>
            <input id="code" className="input-box" placeholder="Prisijungimo vardas" type="text" value={nt} onChange={event => setUN(event.target.value)} maxLength="20"/>
          </div>
          <div>
            <label className="name">Slaptažodis</label>
            <input id="password" className="input-box pw" placeholder="Slaptažodis" type="password" value={rt} onChange={event => setPW(event.target.value)} maxLength="20"/>
          </div>     
          <div id="login-error"/>
          <div className="buttons">
            <button id="autobutton" className="menubutton" onClick={() => getLogin("User", "userid")}>Prisijungti</button>
            <button className="menubutton" onClick={() => ReactDOM.render(<Start />, document.getElementById('root'))}>Grįžti atgal</button>
          </div>
        </div>
      </div>
    </div>
  );
}
export default Login;