import firebase from 'firebase';
import '../ControlPanelStyle.css';
import maildata from '../maildata.json';

var GetUser;
var VacationAdd;
var VacationAddK;
var VacationAddKa;
var VacationID;
var Data1;
var Data2;
var Data3;
var Vacation;
var vardas;
var pavarde;
var check;
var imone;
var dienos;
var email;
var wdsum;
var role;
var user;
var newID;
var count;
export function AddVacation(UID, type, today, SD, ED, duration, comment){
    check = 0;
    wdsum = 0;
    count = 0;
    role = document.getElementById("user-role").innerHTML;
    if((role === "Vadovas(-ė)")||(role === "Buhalteris(-ė)")){
        user = "Staff";
    }
    else if(role === "Darbuotojas(-a)"){
        user = "User";
    }
    return(
        GetUser = firebase.database().ref(user).orderByKey().equalTo(UID),
        GetUser.once('value', (snapshot) => {
            snapshot.forEach((childSnapshot) => {
                Data1 = childSnapshot.val();
            });
            vardas = Data1.name;
            pavarde = Data1.surname;
            imone = Data1.company;
            email = Data1.email;
            if((role === "Vadovas(-ė)")||(role === "Buhalteris(-ė)")){
                imone = Data1.workCompany;
            }
            else if(role === "Darbuotojas(-a)"){
                imone = Data1.company;
            }
            VacationAdd = firebase.database().ref("Vacations").orderByChild("user").equalTo(UID);
            VacationAdd.once('value', (snapshot) => {
                snapshot.forEach((childSnapshot) => {
                    Data2 = childSnapshot.val();
                    if(((Data2.startDate >= SD)&&(Data2.startDate <= ED))||((Data2.endDate >= SD)&&(Data2.endDate <= ED))){
                        check = 1;
                        document.getElementById("vacation-result").innerHTML = "Jūs jau esate pateikę atostogų prašymą";
                        document.getElementById("vacation-result").style.color = "red";
                    }
                    else if(Data2 === undefined){
                        wdsum = 0;
                    }
                    else if((type === "Kasmetinės atostogos")&&(Data2.status !== 3)){
                        wdsum = wdsum + Data2.workDays;
                    }
                });
                var date3 = new Date(SD);  
                var date4 = new Date(ED); 
                var time_dif = date4.getTime() - date3.getTime();
                var date_diff = time_dif / (1000 * 60 * 60 * 24);
                date_diff = Math.round(date_diff);
                var wdate = new Date();
                var wdw;
                var wdays = 0;
                for(var j = 0; j <= date_diff; j++){
                    wdw = wdate.getDay();
                    wdays = wdays + 1;
                    if(wdw % 6 === 0){wdays = wdays - 1;}
                    if((wdate.getMonth() === 0)&&(wdate.getDate() === 1)&&(wdw % 6 !== 0)){wdays = wdays - 1;}
                    if((wdate.getMonth() === 1)&&(wdate.getDate() === 16)&&(wdw % 6 !== 0)){wdays = wdays - 1;}
                    if((wdate.getMonth() === 2)&&(wdate.getDate() === 11)&&(wdw % 6 !== 0)){wdays = wdays - 1;}
                    if((wdate.getMonth() === 3)&&(wdate.getDate() === 1)&&(wdw % 6 !== 0)){wdays = wdays - 1;}
                    if((wdate.getMonth() === 4)&&(wdate.getDate() === 1)&&(wdw % 6 !== 0)){wdays = wdays - 1;}
                    if((wdate.getMonth() === 5)&&(wdate.getDate() === 24)&&(wdw % 6 !== 0)){wdays = wdays - 1;}
                    if((wdate.getMonth() === 6)&&(wdate.getDate() === 6)&&(wdw % 6 !== 0)){wdays = wdays - 1;}
                    if((wdate.getMonth() === 7)&&(wdate.getDate() === 15)&&(wdw % 6 !== 0)){wdays = wdays - 1;}
                    if((wdate.getMonth() === 10)&&(wdate.getDate() === 1 )&&(wdw % 6 !== 0)){wdays = wdays - 1;}
                    if((wdate.getMonth() === 10)&&(wdate.getDate() === 2 )&&(wdw % 6 !== 0)){wdays = wdays - 1;}
                    if((wdate.getMonth() === 11)&&(wdate.getDate() === 24 )&&(wdw % 6 !== 0)){wdays = wdays - 1;}
                    if((wdate.getMonth() === 11)&&(wdate.getDate() === 25 )&&(wdw % 6 !== 0)){wdays = wdays - 1;}
                    if((wdate.getMonth() === 11)&&(wdate.getDate() === 26 )&&(wdw % 6 !== 0)){wdays = wdays - 1;}
                    wdate.setDate(wdate.getDate() + 1);
                }
                var tempdate = new Date();
                var TD = tempdate.getMonth() + " " + tempdate.getDate() + " " + tempdate.getFullYear();
                var date1 = new Date(Data1.regDate);  
                var date2 = new Date(TD); 
                var time_diff = date2.getTime() - date1.getTime();
                var DD = time_diff / (1000 * 60 * 60 * 24);
                DD = Math.round(DD);
                var date = new Date();
                var WE;
                var days = 0;
                for(var i = 0; i <= DD; i++){
                    WE = date.getDay();
                    days = days + 0.079365;
                    if(WE % 6 === 0){days = days - 0.079365;}
                    if((date.getMonth() === 0)&&(date.getDate() === 1)&&(WE % 6 !== 0)){days = days - 0.079365;}
                    if((date.getMonth() === 1)&&(date.getDate() === 16)&&(WE % 6 !== 0)){days = days - 0.079365;}
                    if((date.getMonth() === 2)&&(date.getDate() === 11)&&(WE % 6 !== 0)){days = days - 0.079365;}
                    if((date.getMonth() === 3)&&(date.getDate() === 1)&&(WE % 6 !== 0)){days = days - 0.079365;}
                    if((date.getMonth() === 4)&&(date.getDate() === 1)&&(WE % 6 !== 0)){days = days - 0.079365;}
                    if((date.getMonth() === 5)&&(date.getDate() === 24)&&(WE % 6 !== 0)){days = days - 0.079365;}
                    if((date.getMonth() === 6)&&(date.getDate() === 6)&&(WE % 6 !== 0)){days = days - 0.079365;}
                    if((date.getMonth() === 7)&&(date.getDate() === 15)&&(WE % 6 !== 0)){days = days - 0.079365;}
                    if((date.getMonth() === 10)&&(date.getDate() === 1 )&&(WE % 6 !== 0)){days = days - 0.079365;}
                    if((date.getMonth() === 10)&&(date.getDate() === 2 )&&(WE % 6 !== 0)){days = days - 0.079365;}
                    if((date.getMonth() === 11)&&(date.getDate() === 24 )&&(WE % 6 !== 0)){days = days - 0.079365;}
                    if((date.getMonth() === 11)&&(date.getDate() === 25 )&&(WE % 6 !== 0)){days = days - 0.079365;}
                    if((date.getMonth() === 11)&&(date.getDate() === 26 )&&(WE % 6 !== 0)){days = days - 0.079365;}
                    date.setDate(date.getDate() + 1);
                }
                dienos = (parseInt(Data1.days) + days).toFixed(3);
                if(check === 0){
                    VacationID = firebase.database().ref("Vacations");
                    VacationID.once('value', (snapshot) => {
                        snapshot.forEach((childSnapshot) => {
                            Data3 = childSnapshot.val();
                        })
                        count = count + 1;
                        if(count === 1){
                            if(Data3 !== undefined){
                                newID = Data3.id + 1;
                            }
                            else {
                                newID = 1;
                            }
                            if((dienos >= (wdsum + wdays))&&(type === "Kasmetinės atostogos")&&(wdays !== 0)){
                                VacationAddK = firebase.database().ref("Vacations");
                                Vacation = VacationAddK.push();
                                Vacation.set({
                                    id: newID,
                                    user: UID,
                                    name: vardas,
                                    surname: pavarde,
                                    type: type,
                                    date: today,
                                    startDate: SD,
                                    endDate: ED,
                                    status: 1,
                                    company: imone,
                                    duration: duration,
                                    workDays: wdays,
                                    confirmedBy: ""
                                });
                                sendEmail(vardas, pavarde, type, email, comment);
                                document.getElementById("vacation-result").innerHTML = "Prašymas sėkmingai išsiųstas";
                                document.getElementById("vacation-result").style.color = "green";
                            }
                            else{
                                document.getElementById("vacation-result").innerHTML = "Nepakanka atostoginių dienų arba pasirinkta data yra savaitgalis.<br/>Turimos dienos: " + dienos + "<br/>Pasirinktų atostogų trukmė dienomis: " + wdays + "<br/>Šio ir kitų dar nepatvirtintų prašymų atostogų trukmė dienomis: " + (wdays + wdsum);
                                document.getElementById("vacation-result").style.color = "red";
                            }
                            if(((type === "Dekretinės atostogos")&&(duration <= 126))||((type === "Tėvystės atostogos")&&(duration <= 30))||((type === "Vaiko priežiūros atostogos")&&(duration <= 1095))||((type === "Mokymosi atostogos")&&(duration <= 30))||((type === "Kūrybinės atostogos")&&(duration <= 365))){
                                VacationAddKa = firebase.database().ref("Vacations");
                                Vacation = VacationAddKa.push();
                                Vacation.set({
                                    id: newID,
                                    user: UID,
                                    name: vardas,
                                    surname: pavarde,
                                    type: type,
                                    date: today,
                                    startDate: SD,
                                    endDate: ED,
                                    status: 1,
                                    company: imone,
                                    duration: duration,
                                    confirmedBy: "",
                                    workDays: "",
                                });
                                sendEmail(vardas, pavarde, type, email, comment);
                                document.getElementById("vacation-result").innerHTML = "Prašymas sėkmingai išsiųstas";
                                document.getElementById("vacation-result").style.color = "green";
                            }
                            else if(type !== "Kasmetinės atostogos"){
                                document.getElementById("vacation-result").innerHTML = "Pasirinkta per daug dienų šiam atostogų tipui";
                                document.getElementById("vacation-result").style.color = "red";
                            }
                        }
                    })
                }
            })
        })
    );
}

function sendEmail(vardas, pavarde, type, email, comment) {
    if(type === "Kasmetinės atostogos"){type = "kasmetinių atostogų"};
    if(type === "Dekretinės atostogos"){type = "dekretinių atostogų"};
    if(type === "Tėvystės atostogos"){type = "tėvystės atostogų"};
    if(type === "Vaiko prižiūros atostogos"){type = "vaiko priežiūros atostogų"};
    if(type === "Mokymosi atostogos"){type = "mokymosi atostogų"};
    if(type === "Kūrybinės atostogos"){type = "kūrybinių atostogų"};
    if(type === "Nemokamos atostogos"){type = "nemokamų atostogų"};
    window.Email.send({
      Host: "smtp.gmail.com",
      Username: maildata.emailname,
      Password: maildata.emailpassword,
      To: email,
      From: maildata.emailname,
      FromName: "Transkelta",
      Subject: "Atostogų prašymas",
      Body: "Darbuotojas " + vardas + " " + pavarde + " nori išeiti " + type + ". Prašome patikrinti programėlę." + comment
    })
    alert("Prašymas sėkmingai išsiųstas");
}