import './ControlPanelStyle.css';
import Dashboard from './pages/Dashboard';
import ReactDOM from 'react-dom';
import DataTable from './components/employees-table';
import DataList from './components/employees-list';
import StaffTable from './components/staff-table';
import Vacation from './pages/Request-Vacation';
import VacationTable from './components/vacation-table';
import VacationTableArc from './components/vacation-archive';
import PastVacationTable from './components/user-vacations';
import ChangePassword from './pages/Change-Password';

import Sick from './pages/Request-Sick';
import React, { useState, useEffect } from 'react';

const NavBar = (props) => {
  const [seconds, setSeconds] = useState(599);
  var [m, setM] = useState("10");
  var [s, setS] = useState("00");

  function reset() {
    setSeconds(600);
  }
  useEffect(() => {
    var sc = "";
    let interval = null;
    interval = setInterval(() => {
      setSeconds(seconds => seconds - 1);
      setM(m => m = Math.floor(seconds / 60));
      if((seconds % 60) < 10){
        sc = "0" + (seconds % 60)
      }
      else {sc = "" + (seconds % 60)}
      setS(s => s = sc);
    }, 1000)
    if (seconds === -1) {
      clearInterval(interval);
      window.location.reload(false);
    }
    return () => clearInterval(interval);
  }, [seconds]);

  return (
    <div>
      <div className="info">
        <div className="cred-row"><div id="user-name" className="user-credentials">Vardas Pavarde</div></div>
        <div id="user-role" className="user-role">Rolė</div>
        <div id="user-days" className="user-role"></div>
      </div>
      <button className="page-buttons" onClick={() => ReactDOM.render(<Dashboard id={props.id}/>, document.getElementById('panelroot'))}>
          Pradinis
        </button>
        <button id="list-users" className="page-buttons" onClick={() => ReactDOM.render(<DataList id={props.id}/>, document.getElementById('panelroot'))}>
          Vartotojai
        </button>
        <button id="edit-users" className="page-buttons" onClick={() => ReactDOM.render(<DataTable />, document.getElementById('panelroot'))}>
          Vartotojai
        </button>
        <button id="list-mods" className="page-buttons" onClick={() => ReactDOM.render(<StaffTable />, document.getElementById('panelroot'))}>
          Moderatoriai
        </button> 
        <button id="request-vacation" className="page-buttons" onClick={() => ReactDOM.render(<Vacation id={props.id}/>, document.getElementById('panelroot'))}>
          Atostogų prašymas
        </button>  
        <button id="request-sick" className="page-buttons" onClick={() => ReactDOM.render(<Sick />, document.getElementById('panelroot'))}>
          Pranešimas apie ligą
        </button>
        <button id="requests-vac" className="page-buttons" onClick={() => ReactDOM.render(<VacationTable id={props.id} />, document.getElementById('panelroot'))}>
          Atostogų prašymai
        </button>
        <button id="archive-vac" className="page-buttons" onClick={() => ReactDOM.render(<VacationTableArc id={props.id} />, document.getElementById('panelroot'))}>
          Atostogų archyvas
        </button>
        <button id="my-vac" className="page-buttons" onClick={() => ReactDOM.render(<PastVacationTable id={props.id} />, document.getElementById('panelroot'))}>
          Mano atostogos
        </button>
        <button id="change-pass" className="page-buttons" onClick={() => ReactDOM.render(<ChangePassword id={props.id} />, document.getElementById('panelroot'))}>
          Keisti slaptažodį
        </button>
      <br/>
      <div className="row">
        <div id="tim">Sesijos laikas: {m}:{s}</div>
        <div className="session"><button className="menubutton" onClick={reset}>Atnaujinti sesija</button></div>
      </div>
    </div>
  );
}

export default NavBar;