import firebase from 'firebase';
import ControlPanel from '../ControlPanel';
import ReactDOM from 'react-dom';

var EmployeesLogin;
var kodas;
var slaptazodis;
var Data;
var CurrentUser;
export function getLogin(login, typ) {
    return(
        kodas = document.getElementById('code').value,
        slaptazodis = document.getElementById('password').value,
        EmployeesLogin = firebase.database().ref(login).orderByChild(typ).equalTo(kodas),
        EmployeesLogin.once('value', (snapshot) => {
            snapshot.forEach((childSnapshot) => {
                CurrentUser = childSnapshot.key;
                Data = childSnapshot.val();
            });
            if((Data !== undefined)&&(slaptazodis === Data.password)&&(Data.password !== "")){
                document.getElementById("login-error").innerHTML = "";
                ReactDOM.render(null, document.getElementById('header'));
                ReactDOM.render(null, document.getElementById('footer'));
                ReactDOM.render(<ControlPanel id={CurrentUser}/>, document.getElementById('root'));
                if((kodas === Data.adminid)&&(slaptazodis === Data.password)&&(login === "Admin")){
                    document.getElementById("user-name").innerHTML = Data.name + " " + Data.surname;
                    document.getElementById("user-role").innerHTML = "Administratorius";
                    document.getElementById("list-users").disabled = true;
                    document.getElementById("list-users").innerHTML = "disabled";
                    document.getElementById("edit-users").disabled = false;
                    document.getElementById("edit-users").innerHTML = "Vartotojai";
                    document.getElementById("list-mods").disabled = false;
                    document.getElementById("list-mods").innerHTML = "Moderatoriai";
                    document.getElementById("request-vacation").disabled = true;
                    document.getElementById("request-vacation").innerHTML = "disabled";
                    document.getElementById("request-sick").disabled = true;
                    document.getElementById("request-sick").innerHTML = "disabled";
                    document.getElementById("requests-vac").disabled = true;
                    document.getElementById("requests-vac").innerHTML = "disabled";
                    document.getElementById("archive-vac").disabled = true;
                    document.getElementById("archive-vac").innerHTML = "disabled";
                    document.getElementById("my-vac").disabled = true;
                    document.getElementById("my-vac").innerHTML = "disabled";
                    document.getElementById("change-pass").disabled = false;
                    document.getElementById("change-pass").innerHTML = "Keisti slaptažodį";
                }
                if((kodas === Data.staffid)&&(slaptazodis === Data.password)&&(Data.role === "buhalteris")){
                    document.getElementById("user-name").innerHTML = Data.name + " " + Data.surname;
                    document.getElementById("user-role").innerHTML = "Buhalteris(-ė)";
                    document.getElementById("list-users").disabled = false;
                    document.getElementById("list-users").innerHTML = "Vartotojai";
                    document.getElementById("edit-users").disabled = true;
                    document.getElementById("edit-users").innerHTML = "disabled";
                    document.getElementById("list-mods").disabled = true;
                    document.getElementById("list-mods").innerHTML = "disabled";
                    document.getElementById("requests-vac").disabled = false;
                    document.getElementById("requests-vac").innerHTML = "Atostogų prašymai";
                    document.getElementById("request-vacation").disabled = false;
                    document.getElementById("request-vacation").innerHTML = "Atostogų prašymas (asmeninis)";
                    document.getElementById("request-sick").disabled = true;
                    document.getElementById("request-sick").innerHTML = "disabled";
                    document.getElementById("archive-vac").disabled = false;
                    document.getElementById("archive-vac").innerHTML = "Atostogų archyvai";
                    document.getElementById("my-vac").disabled = true;
                    document.getElementById("my-vac").innerHTML = "disabled";
                    document.getElementById("change-pass").disabled = false;
                    document.getElementById("change-pass").innerHTML = "Keisti slaptažodį";
                }
                if((kodas === Data.staffid)&&(slaptazodis === Data.password)&&(Data.role === "vadovas")){
                    document.getElementById("user-name").innerHTML = Data.name + " " + Data.surname;
                    document.getElementById("user-role").innerHTML = "Vadovas(-ė)";
                    document.getElementById("list-users").disabled = false;
                    document.getElementById("list-users").innerHTML = "Vartotojai";
                    document.getElementById("edit-users").disabled = true;
                    document.getElementById("edit-users").innerHTML = "disabled";
                    document.getElementById("list-mods").disabled = true;
                    document.getElementById("list-mods").innerHTML = "disabled";
                    document.getElementById("requests-vac").disabled = false;
                    document.getElementById("requests-vac").innerHTML = "Atostogų prašymai";
                    document.getElementById("request-vacation").disabled = false;
                    document.getElementById("request-vacation").innerHTML = "Atostogų prašymas (asmeninis)";
                    document.getElementById("request-sick").disabled = true;
                    document.getElementById("request-sick").innerHTML = "disabled";
                    document.getElementById("archive-vac").disabled = true;
                    document.getElementById("archive-vac").innerHTML = "disabled";
                    document.getElementById("my-vac").disabled = true;
                    document.getElementById("my-vac").innerHTML = "disabled";
                    document.getElementById("change-pass").disabled = false;
                    document.getElementById("change-pass").innerHTML = "Keisti slaptažodį";
                }
                if((kodas === Data.userid)&&(slaptazodis === Data.password)&&(login === "User")){
                    document.getElementById("user-name").innerHTML = Data.name + " " + Data.surname;
                    document.getElementById("user-role").innerHTML = "Darbuotojas(-a)";
                    var tempdate = new Date();
                    var TD = tempdate.getMonth() + " " + tempdate.getDate() + " " + tempdate.getFullYear();
                    var date1 = new Date(Data.regDate);  
                    var date2 = new Date(TD); 
                    var time_diff = date2.getTime() - date1.getTime();
                    var DD = time_diff / (1000 * 60 * 60 * 24);
                    DD = Math.round(DD);
                    var date = new Date();
                    var WE;
                    var days = 0;
                    for(var i = 0; i <= DD; i++){
                        WE = date.getDay();
                        days = days + 0.079365;
                        if(WE % 6 === 0){days = days - 0.079365;}
                        if((date.getMonth() === 0)&&(date.getDate() === 1)&&(WE % 6 !== 0)){days = days - 0.079365;}
                        if((date.getMonth() === 1)&&(date.getDate() === 16)&&(WE % 6 !== 0)){days = days - 0.079365;}
                        if((date.getMonth() === 2)&&(date.getDate() === 11)&&(WE % 6 !== 0)){days = days - 0.079365;}
                        if((date.getMonth() === 3)&&(date.getDate() === 1)&&(WE % 6 !== 0)){days = days - 0.079365;}
                        if((date.getMonth() === 4)&&(date.getDate() === 1)&&(WE % 6 !== 0)){days = days - 0.079365;}
                        if((date.getMonth() === 5)&&(date.getDate() === 24)&&(WE % 6 !== 0)){days = days - 0.079365;}
                        if((date.getMonth() === 6)&&(date.getDate() === 6)&&(WE % 6 !== 0)){days = days - 0.079365;}
                        if((date.getMonth() === 7)&&(date.getDate() === 15)&&(WE % 6 !== 0)){days = days - 0.079365;}
                        if((date.getMonth() === 10)&&(date.getDate() === 1 )&&(WE % 6 !== 0)){days = days - 0.079365;}
                        if((date.getMonth() === 10)&&(date.getDate() === 2 )&&(WE % 6 !== 0)){days = days - 0.079365;}
                        if((date.getMonth() === 11)&&(date.getDate() === 24 )&&(WE % 6 !== 0)){days = days - 0.079365;}
                        if((date.getMonth() === 11)&&(date.getDate() === 25 )&&(WE % 6 !== 0)){days = days - 0.079365;}
                        if((date.getMonth() === 11)&&(date.getDate() === 26 )&&(WE % 6 !== 0)){days = days - 0.079365;}
                        date.setDate(date.getDate() + 1);
                    }
                    document.getElementById("user-days").innerHTML = "Atostogų dienos: " + (parseInt(Data.days) + days).toFixed(3);
                    document.getElementById("list-users").disabled = true;
                    document.getElementById("list-users").innerHTML = "disabled";
                    document.getElementById("edit-users").disabled = true;
                    document.getElementById("edit-users").innerHTML = "disabled";
                    document.getElementById("list-mods").disabled = true;
                    document.getElementById("list-mods").innerHTML = "disabled";
                    document.getElementById("requests-vac").disabled = true;
                    document.getElementById("requests-vac").innerHTML = "disabled";
                    document.getElementById("request-vacation").disabled = false;
                    document.getElementById("request-vacation").innerHTML = "Atostogų prašymas";
                    document.getElementById("request-sick").disabled = false;
                    document.getElementById("request-sick").innerHTML = "Pranešimas apie ligą";
                    document.getElementById("archive-vac").disabled = true;
                    document.getElementById("archive-vac").innerHTML = "disabled";
                    document.getElementById("my-vac").disabled = false;
                    document.getElementById("my-vac").innerHTML = "Mano atostogos";
                    document.getElementById("change-pass").disabled = false;
                    document.getElementById("change-pass").innerHTML = "Keisti slaptažodį";
                }
                if(document.getElementById("user-role").innerHTML === "Rolė" ){
                    document.getElementById("list-users").disabled = true;
                    document.getElementById("list-users").innerHTML = "disabled";
                    document.getElementById("edit-users").disabled = true;
                    document.getElementById("edit-users").innerHTML = "disabled";
                    document.getElementById("list-mods").disabled = true;
                    document.getElementById("list-mods").innerHTML = "disabled";
                    document.getElementById("requests-vac").disabled = true;
                    document.getElementById("requests-vac").innerHTML = "disabled";
                    document.getElementById("request-vacation").disabled = true;
                    document.getElementById("request-vacation").innerHTML = "disabled";
                    document.getElementById("request-sick").disabled = true;
                    document.getElementById("request-sick").innerHTML = "disabled";
                    document.getElementById("archive-vac").disabled = true;
                    document.getElementById("archive-vac").innerHTML = "disabled";
                    document.getElementById("my-vac").disabled = true;
                    document.getElementById("my-vac").innerHTML = "disabled";
                    document.getElementById("change-pass").disabled = true;
                    document.getElementById("change-pass").innerHTML = "disabled";
                }
            }            
            else document.getElementById("login-error").innerHTML = "Blogi prisijungimo duomenys";
        })
    );  
};