import React, { useState } from 'react';
import '../ControlPanelStyle.css';

export default function Sick() {
  var t;
  var nt;
  const [title, setTitle] = useState('');
  nt = "";
  for(var i = 0; i < title.length; i++){
    t = title.charAt(i);
    if((t === "0") || (t === "1") || (t === "2") || (t === "3") || (t === "4") || (t === "5") || (t === "6") || (t === "7") || (t === "8") || (t === "9")){
      nt = nt + t;
    }
  }
  return(
    <div className="dashboard-page">
        <h2>Pranešimas apie susirgimą</h2>
        <input id="di" value={nt} type="text" onChange={event => setTitle(event.target.value)}></input>
        <button>asdf</button>
    </div>
  );
};