import '../style.css';
import ReactDOM from 'react-dom';
import Start from './Start';
import firebase from 'firebase';
import React, { useState } from 'react';

var EmployeesRegUser;
var EmployeesReg;
var kodas;
var password;
var Data;
export function RegCheck(key) {
  return(
    kodas = document.getElementById('code').value,
    password = document.getElementById('password').value,
    EmployeesRegUser = firebase.database().ref("User").orderByChild("userid").equalTo(kodas),
    EmployeesRegUser.once('value', (snapshot) => {
      snapshot.forEach((childSnapshot) => {
          Data = childSnapshot.val();
      });
      if(Data === undefined){
        if((kodas.length >= 8)&&(kodas.length <= 20)){
          if((password.length >= 8)&&(password.length <= 20)){
            EmployeesReg = firebase.database().ref("User/" + key);
            EmployeesReg.update({
              userid: kodas,
              password: password
            });
            alert("Vartotojas sekmingai priregistruotas");
            ReactDOM.render(<Start />, document.getElementById('root'));
          }
          else document.getElementById("reg-fin").innerHTML = "Vartotojo slaptažodis turi buti nuo 8 iki 20 simbolių";
        }
        else document.getElementById("reg-fin").innerHTML = "Vartotojo vardas turi buti nuo 8 iki 20 simbolių";
      }
      else document.getElementById("reg-fin").innerHTML = "Toks vartotojo vardas jau yra";
    })  
  );  
};

function RegisterFinish(props) {
  var t;
  var nt;
  var rt;
  var usern = /^[0-9a-zA-Z-_.]+$/;
  var passw = /^[0-9a-zA-Z]+$/;
  const [UN, setUN] = useState('');
  nt = "";
  for(var i = 0; i < UN.length; i++){
    t = UN.charAt(i);
    if(t.match(usern)){
      nt = nt + t;
    }
  }
  const [PW, setPW] = useState('');
  rt = "";
  for(var j = 0; j < PW.length; j++){
    t = PW.charAt(j);
    if(t.match(passw)){
      rt = rt + t;
    }
  }
  return (
    <div className="container page-login">
      <div className="small-container">
        <div id="user" className="content">
          <h2>Registracija</h2>
          <div>
            <div className="name">Norimas prisijungimo vardas</div>
            <input id="code" placeholder="Prisijungimo vardas" className="input-box" type="text" value={nt} onChange={event => setUN(event.target.value)} maxLength="20" />
          </div>
          <div>
            <div className="password">Slaptažodis</div>
            <input id="password" placeholder="Slaptažodis" className="input-box pw" type="password" value={rt} onChange={event => setPW(event.target.value)} maxLength="20" />
          </div>
          <div id="reg-fin"></div>
          <div className="buttons">
            <button id="autobutton" className="menubutton" onClick={() => RegCheck(props.id)}>Registruotis</button>
            <button className="menubutton" onClick={() => ReactDOM.render(<Start />, document.getElementById('root'))}>Grįžti atgal</button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default RegisterFinish;